* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Proxima Nova", sans-serif;
}

body {
  background-color: white;
  overflow-x: hidden;
}

ul li {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

.container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0;
  position: relative;
}

.main-section {
  min-height: calc(100vh - 360px + 120px);
  width: 100%;
}

.bg-light-creme {
  background-color: #fffffe;
}

.bg-main {
  background-color: #ff8e3c;
}

.bg-secondary {
  background-color: #ff8e3c;
}

.hidden {
  display: none;
}

.visible {
  display: block !important;
}

.flex-visible {
  display: flex !important;
}

.relative {
  position: relative;
  z-index: 50;
}

.noscroll {
  overflow: hidden;
}

@media (min-width: 530px) {
  .container {
    padding: 0 16px;
  }
}
@media (min-width: 530px) {
  .main-section {
    min-height: calc(100vh - 360px + 100px);
    width: 100%;
  }
}
header {
  background-color: #ff8e3c;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 120px;
  padding: 0 24px;
  position: relative;
}

.toggle-hamburger {
  position: absolute;
  right: 16px;
  display: block;
  cursor: pointer;
  font-size: 1.8em;
  z-index: 502;
  color: #eff0f3;
}
.toggle-hamburger .toggle-hamburger-svg-open {
  width: 30px;
  height: auto;
}
.toggle-hamburger .toggle-hamburger-svg-close {
  width: 30px;
  height: auto;
  display: none;
}

.nav {
  display: relative;
}
.nav_desktop {
  display: none;
}
.nav_mobile {
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 501;
  background-color: #ff8e3c;
  opacity: 0.9;
  min-width: 70vw;
  justify-content: center;
  align-items: center;
  padding: 50px 50px;
  border-radius: 20px;
  border: 2px solid rgb(109, 107, 107);
  -webkit-box-shadow: inset 0px 0px 9px 2px #ff8e3c;
  box-shadow: inset 0px 0px 9px 2px #ff8e3c;
}
.nav_mobile ul {
  display: flex;
  flex-direction: column;
  gap: 35px;
  text-align: center;
  color: #0d0d0d;
}
.nav_mobile ul li a {
  font-size: 1.1em;
  font-weight: 600;
}
.nav_mobile ul li a:hover {
  color: #eff0f3;
}

.overlay {
  position: fixed;
  overflow-y: hidden;
  overscroll-behavior: contain;
  inset: 0;
  z-index: 101;
  background: #2a2a2a;
  opacity: 0.75;
  display: none;
}

@media (min-width: 960px) {
  header .toggle-hamburger {
    position: absolute;
    right: 16px;
    display: none;
    cursor: pointer;
    font-size: 1.8em;
    z-index: 502;
  }
  .nav_desktop {
    display: flex;
  }
  .nav_desktop ul {
    display: flex;
    gap: 3em;
  }
  .nav_desktop ul li a {
    color: #2a2a2a;
    font-size: 1em;
    font-weight: 700;
    padding: 8px 16px;
    border-radius: 35px;
    cursor: pointer;
    transition: all 0.3s ease-out;
  }
  .nav_desktop ul li a:hover {
    color: #ff8e3c;
    font-size: 1em;
    background-color: #eff0f3;
  }
  .nav_desktop ul li a.active {
    color: #ff8e3c;
    font-size: 1em;
    background-color: #eff0f3;
  }
  .nav_mobile {
    display: none;
  }
}
.logo-header {
  display: flex;
  align-items: flex-start;
  justify-content: center;
}

.site-footer {
  padding: 2rem 0 3.5rem 0;
  background-color: #eff0f3;
  height: 500px;
}
.site-footer p {
  font-size: 0.9rem;
}
.site-footer .headline {
  color: #ff8e3c;
  opacity: 0.8;
  margin-bottom: 0.33rem;
}
.site-footer__link {
  color: #999;
  text-decoration: none;
}
.site-footer__link:hover {
  color: #ff8e3c;
  text-decoration: underline;
}
.site-footer__inner {
  border-top: 1px dotted #ff8e3c;
  padding-top: 3.5rem;
}
.site-footer__col-one {
  text-align: center;
}
@media (min-width: 960px) {
  .site-footer__col-one {
    text-align: left;
    width: 33%;
    float: left;
  }
}
.site-footer__col-two-three-group {
  text-align: center;
  width: 85%;
  margin: 0 auto;
}
@media (min-width: 960px) {
  .site-footer__col-two-three-group {
    text-align: left;
    float: left;
    width: 42%;
    margin: 0;
  }
}
.site-footer__col-two {
  width: 50%;
  float: left;
}
.site-footer__col-three {
  width: 50%;
  float: left;
}
.site-footer__col-four {
  padding-top: 20px;
  width: 85%;
  margin: 0 auto;
  clear: both;
  text-align: center;
}
@media (min-width: 960px) {
  .site-footer__col-four {
    padding-top: 0;
    margin: 0;
    clear: none;
    text-align: left;
    width: 25%;
    float: right;
  }
}

.group:after {
  clear: both;
}

.group:before,
.group:after {
  content: " ";
  display: table;
}

.headline {
  margin: 0;
  font-family: "Poppins", sans-serif;
  font-weight: 300;
}

.headline--large {
  font-size: 3.6rem;
}

@media (min-width: 530px) {
  .headline--large {
    font-size: 6.25rem;
  }
}
.headline--large-medium {
  font-size: 5rem;
  margin: 0 0 1rem 0;
}

.headline--medium {
  font-size: 1.9rem;
  margin-bottom: 0.75rem;
}

@media (min-width: 530px) {
  .headline--medium {
    font-size: 3.125rem;
  }
}
.headline--small-plus {
  font-family: "Poppins", sans-serif;
  font-size: 1.6875rem;
  font-weight: 400;
  margin-bottom: 1.9rem;
}

.headline--small {
  font-size: 1.2rem;
  margin-bottom: 1.9rem;
}

@media (min-width: 530px) {
  .headline--small {
    font-size: 1.6875rem;
  }
}
.headline--smaller {
  font-size: 1.5rem;
}

.headline--tiny {
  font-family: "Poppins", sans-serif;
  font-size: 1.3875rem;
  margin-bottom: 0.25rem;
}

.nav-list ul {
  padding: 0;
  margin: 0;
}

.nav-list li {
  list-style: none;
}

.nav-list a {
  font-size: 0.9rem;
  color: rgb(109, 107, 107);
  text-decoration: none;
}

.nav-list a:hover {
  color: #ff8e3c;
  text-decoration: underline;
}

.min-list {
  padding: 0;
  margin: 0;
}

.min-list li {
  list-style: none;
}

.social-icons-list {
  display: flex;
  gap: 12px;
  margin: 0 4px;
  justify-content: center;
}

.social-icons-list li {
  flex: 0;
  margin: 0 8px;
}

.social-icons-list li a {
  display: block;
  text-align: center;
  color: #fff;
}

.social-icons-list li a:hover {
  opacity: 0.75;
}

.social-color-facebook {
  background-color: #4862a3;
}

.social-color-twitter {
  background-color: #55acee;
}

.social-color-youtube {
  background-color: #cc1e1f;
}

.social-color-linkedin {
  background-color: #0077b5;
}

.social-color-instagram {
  background-color: #d8226b;
}

.title-footer {
  color: #ff8e3c;
  font-size: 2.1em;
  line-height: 1;
  margin-bottom: 1.2em;
  text-shadow: 1px 2px 5px rgb(109, 107, 107);
}

@media (min-width: 767px) {
  .site-footer {
    height: 500px;
  }
}
@media (min-width: 960px) {
  .social-icons-list {
    justify-content: flex-start;
  }
  .site-footer {
    height: 300px;
  }
}
.search-overlay {
  overflow-y: auto;
  overflow-x: hidden;
  z-index: 50000;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ff8e3c;
  visibility: hidden;
  opacity: 0;
  transform: scale(1.09);
  transition: opacity 0.3s, transform 0.3s, visibility 0.3s;
  box-sizing: border-box;
}
.search-overlay p {
  padding-top: 15px;
}
.search-overlay .event-summary p {
  padding-top: 0;
}
.search-overlay .event-summary {
  margin-bottom: 0;
  margin-top: 20px;
}
body.admin-bar .search-overlay {
  top: 32px;
}
.search-overlay__top {
  background-color: rgba(0, 0, 0, 0.12);
}
.search-overlay__icon {
  margin-right: 10px;
  font-size: 1.8rem;
  color: rgba(245, 162, 8, 0.966);
}
.search-overlay--active {
  visibility: visible;
  opacity: 1;
  transform: scale(1);
}
.search-overlay__section-title {
  margin: 30px 10px 1px 0;
  font-weight: 400;
  color: #0d0d0d;
  font-size: 2rem;
  padding: 15px 0;
  border-bottom: 1px solid #ccc;
}
.search-overlay__close {
  position: absolute;
  top: 5px;
  right: 16px;
  font-size: 2.1rem;
  cursor: pointer;
  transition: all 0.3s;
  background-color: #fff;
  color: rgba(245, 162, 8, 0.966);
  line-height: 0.7;
}
.search-overlay__close:hover {
  opacity: 1;
}
.search-overlay .one-half {
  padding-bottom: 0;
}

.search-overlay__section-title {
  margin: 30px 0 1px 0;
  font-weight: 400;
  color: #0D3B66;
  font-size: 2rem;
  padding: 15px 0;
  border-bottom: 1px solid #ccc;
}

.search-term {
  width: 75%;
  box-sizing: border-box;
  border: none;
  padding: 15px 0;
  margin: 0;
  background-color: transparent;
  font-size: 1rem;
  font-weight: 300;
  outline: none;
  color: aliceblue;
}

.body-no-scroll {
  overflow: hidden;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.spinner-loader {
  margin-top: 45px;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  border: 0.25rem solid rgba(0, 0, 0, 0.2);
  border-top-color: black;
  -webkit-animation: spin 1s infinite linear;
  animation: spin 1s infinite linear;
}

.min-list {
  padding: 0;
  margin: 0;
}

.min-list li {
  list-style: none;
}

.link-list li {
  padding: 1rem 0;
  font-size: 1.5rem;
  border-bottom: 1px dotted #DEDEDE;
}

.search-overlay .link-list li {
  font-size: 1.3rem;
}

.link-list li:last-child {
  border-bottom: none;
}

.link-list a {
  display: flex;
  align-items: flex-end;
  gap: 5px;
  color: aliceblue;
}

.search-form-row {
  display: flex;
}

@media (min-width: 960px) {
  .row {
    margin-right: -50px;
  }
  .one-half,
  .one-third,
  .two-thirds,
  .one-fourth,
  .one-fifth,
  .one-sixth {
    padding-bottom: 0;
    float: left;
    box-sizing: border-box;
    padding-right: 50px;
  }
  .two-thirds {
    width: 66.66%;
  }
  .one-half {
    width: 50%;
  }
  .one-third {
    width: 33.33%;
  }
  .one-quarter {
    width: 25%;
  }
  .one-fifth {
    width: 20%;
  }
  .one-sixth {
    width: 16.66%;
  }
}
/* Seach Form */
.search-form {
  background-color: #ff8e3c;
  padding: 20px;
  margin-top: 60px;
  text-align: center;
}

.search-form label {
  display: block;
}

.search-form-row {
  display: flex;
  margin-top: 25px;
  justify-content: center;
}

.search-form #s {
  min-width: 20px;
  flex-basis: auto;
  flex-grow: 1;
  border: none;
  font-family: "Roboto", sans-serif;
  padding: 10px;
}

@media (min-width: 530px) {
  .search-form #s {
    font-size: 1.4rem;
  }
}
.search-submit {
  color: #fff;
  border: none;
  border-radius: 4px;
  outline: none;
  background-color: #0d0d0d;
  padding: 0 20px;
  margin-left: 10px;
}

.my-search-svg {
  color: #2a2a2a;
  width: 16px;
  height: auto;
  font-weight: 600;
}
.my-search-svg:hover {
  color: #ff8e3c;
}
.my-search-svg a span svg {
  width: 14px;
  height: auto;
}

.js-search-trigger {
  margin-right: 15px;
}
.js-search-trigger:hover .my-search-svg {
  color: #ff8e3c;
}

.search-overlay__close {
  color: rgba(245, 162, 8, 0.966);
  margin-top: 3px;
  width: 35px;
  height: auto;
}

.nav_mobile .my-search-svg {
  margin: auto;
  color: #0d0d0d;
  width: 22px;
  height: auto;
  font-weight: 800;
}
.nav_mobile .my-search-svg:hover {
  color: #ff8e3c;
}

.my-search-svg-opened {
  color: #2a2a2a;
}