
.site-footer {
  padding: 2rem 0 3.5rem 0;
  background-color: $light;
  height: $footerHeightMedium;
  p {
    font-size: 0.9rem;
  }

  .headline {
    color: $primary;
    opacity: 0.8;
    margin-bottom: 0.33rem;
  }

  &__link {
    color: #999;
    text-decoration: none;
  }

  &__link:hover {
    color: $primary;
    text-decoration: underline;
  }

  &__inner {
    border-top: 1px dotted $primary;
    padding-top: 3.5rem;
  }

  &__col-one {
    text-align: center;
    @include atMedium {
      text-align: left;
      width: 33%;
      float: left;
    }
  }

  &__col-two-three-group {
    text-align: center;
    width: 85%;
    margin: 0 auto;
    @include atMedium {
      text-align: left;
      float: left;
      width: 42%;
      margin: 0;
    }
  }

  &__col-two {
    width: 50%;
    float: left;
  }
  &__col-three {
    width: 50%;
    float: left;
  }
  &__col-four {
    padding-top: 20px;
    width: 85%;
    margin: 0 auto;
    clear: both;
    text-align: center;
    @include atMedium {
      padding-top: 0;
      margin: 0;
      clear: none;
      text-align: left;
      width: 25%;
      float: right;
    }
  }
}

.group:after {
  clear: both;
}
.group:before,
.group:after {
content: " ";
display: table;
}

.headline {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  font-weight: 300;
}
.headline--large {
  font-size: 3.6rem;
}
@media (min-width: 530px) {
  .headline--large {
    font-size: 6.25rem;
  }
}
.headline--large-medium {
  font-size: 5rem;
  margin: 0 0 1rem 0;
}
.headline--medium {
  font-size: 1.9rem;
  margin-bottom: 0.75rem;
}
@media (min-width: 530px) {
  .headline--medium {
    font-size: 3.125rem;
  }
}
.headline--small-plus {
  font-family: 'Poppins', sans-serif;
  font-size: 1.6875rem;
  font-weight: 400;
  margin-bottom: 1.9rem;
}
.headline--small {
  font-size: 1.2rem;
  margin-bottom: 1.9rem;
}
@media (min-width: 530px) {
  .headline--small {
    font-size: 1.6875rem;
  }
}
.headline--smaller {
  font-size: 1.5rem;
}
.headline--tiny {
  font-family: 'Poppins', sans-serif;
  font-size: 1.3875rem;
  margin-bottom: 0.25rem;
}

.nav-list ul {
  padding: 0;
  margin: 0;
}

.nav-list li {
  list-style: none;
}

.nav-list a {
  font-size: 0.9rem;
  color: $gray;
  text-decoration: none;
}

.nav-list a:hover {
  color: $primary;
  text-decoration: underline;
}

.min-list {
  padding: 0;
  margin: 0;
}

.min-list li {
  list-style: none;
}

.social-icons-list {
  display: flex;
  gap: 12px;
  margin: 0 4px;
  justify-content: center;
}


.social-icons-list li {
  flex: 0;
  margin: 0 8px;
}

.social-icons-list li a {
  display: block;
  text-align: center;
  color: #fff;
  // font-size: 1.25rem;
}

.social-icons-list li a:hover {
  opacity: 0.75;
}

.social-color-facebook {
  background-color: #4862a3;
}

.social-color-twitter {
  background-color: #55acee;
}

.social-color-youtube {
  background-color: #cc1e1f;
}

.social-color-linkedin {
  background-color: #0077b5;
}

.social-color-instagram {
  background-color: #d8226b;
}

.title-footer {
  color: $primary;
  font-size: 2.1em;
  line-height: 1; 
  margin-bottom: 1.2em;
  text-shadow: 1px 2px 5px $gray;
}


@include atSmallMedium {
  .site-footer {
      height: $footerHeightMedium;
  }
}

@include atMedium {
.social-icons-list {
  justify-content: flex-start;
}
.site-footer {
  height: $footerHeightLarge;
}
}